<template>
<div>
    <div class="stepsnav">
        <el-steps :active="active" align-center>
        <el-step title="" ></el-step>
        <el-step title="" ></el-step>
        <el-step title="" ></el-step>
        </el-steps>
    </div>
    <div class="step1" v-if="active==1">
        <el-input  placeholder="" v-model="organ_name" class="inputstyle"></el-input>
        <div class="selecttype">
            <template >
                <el-select v-model="defaultvalue" placeholder="">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
                </el-select>
            </template>
        </div>
        <el-input  placeholder="" v-model="organ_admin" class="inputstyle"></el-input>
        <el-input  placeholder="" v-model="organ_contact" class="inputstyle"></el-input>
        <el-input type="textarea" v-model="organ_introduce" placeholder="" class="inputstyle"></el-input>
        <div class="upload">
            <el-upload
            class="upload-demo"
            action="a"
            >
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">,</div>
            </el-upload>   
        </div>
          <el-button type="primary" class="nextbtn" @click="next"></el-button>
    </div>
    <div class="step2" v-if="active==2">
        <div>
            <img src="@/assets/vote.png" class="voteimg"/>
            <div class="votetitle">
                {{endvotedate}}
            </div>
            <div class="voteshort">
                :
            </div>
        </div>
        <el-button type="primary" class="nextbtn" @click="next"></el-button>
    </div>
    <div class="step2" v-if="active==4">
        <div>
            <img src="@/assets/votesuccess.png" class="voteimg"/>
            <div class="votesuccess">
                WWF
            </div>
            <div class="votedetail">
                <span class="stylefont"></span>
                <br/>
                :20  :3
                <br/>
                <br/>
                <span class="stylefont"></span>
                <br/>
                :134 :16
            </div>
        </div>
        <el-button type="primary" class="nextbtn" @click="next"></el-button>
    </div>
</div>


</template>

<script>
import axios from 'axios';
export default {

  name: 'HelloWorld',
  data () {
    return {
        active: 1,
        defaultvalue:"",
        endvotedate:"",
        organ_name:"",
        organ_admin:"",
        organ_contact:"",
        organ_introduce:"",
        options: [ {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }, {
          value: '6',
          label: ''
        }, {
          value: '7',
          label: ''
        }, {
          value: '8',
          label: ''
        }, {
          value: '9',
          label: ''
        }, {
          value: '10',
          label: ''
        }],
    }
  },methods:{
    next() {
     
       switch(this.active){
           case 1:
              this.active=this.active+1;
           break;
           case 2:
              this.active=this.active+2;
           break;
           default:
              this.active=1;
           break;
       }

 

      this.getNowFormatDate();
    }, getNowFormatDate() {
        var date = new Date();
        date.setDate(date.getDate() + 3);
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentdate = year + "" + month + ""+ strDate+ "" ;
        this.endvotedate=currentdate;
       
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.stepsnav{
    width: 80%;
    margin: 0px auto;
    margin-top:30px;
}
.inputstyle{
    width: 80%;
    margin: 0px auto;
    margin-top: 20px;
}
.selecttype{
    width: 80%;
    margin: 0px auto;
    margin-top: 20px;
    text-align: left;
}
.upload{
    width: 80%;
    text-align: left;
    margin: 0px auto;
    margin-top: 20px;
}
.nextbtn{
    width: 40%;
    margin: 0px auto;
    margin-top: 40px;
    text-align: center;
}
.step1,.step2{
    padding-bottom: 40px;
}
.voteimg{
    width: 20%;
    display: block;
    margin: 0px auto;
    margin-top: 30px;
}
.votetitle{
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    padding-top: 10px;
}
.voteshort{
    font-size: 14px;
    color: #969696;
    width: 70%;
    margin: 0px auto;
    text-align: justify;
    font-size: 12px;
    padding-top: 14px;
}
.votesuccess{
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    padding-top: 10px;
}
.votedetail{
    font-size: 14px;
    width: 70%;
    margin: 0px auto;
    text-align: center;
    font-size: 12px;
    padding-top: 14px;
}
.stylefont{
    font-weight: bold;
}
</style>



